h1 {
  text-transform: uppercase;
  font-size: 40px;
  line-height: 52px;
}

@media (width >= 640px) {
  h1 {
    font-size: 48px;
    line-height: 62px;
  }
}

@media (width >= 1280px) {
  h1 {
    font-size: 64px;
    line-height: 78px;
  }
}

h2 {
  text-transform: uppercase;
  font-size: 30px;
  line-height: 40px;
}

@media (width >= 640px) {
  h2 {
    font-size: 36px;
    line-height: 46px;
  }
}

@media (width >= 1280px) {
  h2 {
    font-size: 56px;
    line-height: 70px;
  }
}

h3 {
  text-transform: uppercase;
  font-size: 18px;
  line-height: 22px;
}

@media (width >= 640px) {
  h3 {
    font-size: 24px;
    line-height: 30px;
  }
}

@media (width >= 1280px) {
  h3 {
    font-size: 34px;
    line-height: 42px;
  }
}

h4 {
  text-transform: uppercase;
  font-size: 22px;
  line-height: 28px;
}

h5 {
  font-size: 16px;
  line-height: 22px;
}

h6 {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 20px;
}

.quote__text {
  font-size: 14px;
  line-height: 20px;
}

@media (width >= 1280px) {
  .quote__text {
    font-size: 24px;
    line-height: 36px;
  }
}

button {
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  outline: none;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
}

.form__field:focus, .form__field::placeholder, .form__field, .page__mainText {
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
}

.form__field:-webkit-autofill:focus {
  font-size: 16px;
  font-weight: normal;
  line-height: 24px;
}

.page__secondaryText {
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
}

.menu__langItem, .menu__specs, .menu__item, .header__langItem, .header__specs {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
}

.page {
  scroll-behavior: smooth;
}

.page__body {
  background-color: #f7f7f7;
  flex-direction: column;
  align-items: center;
  font-family: Inter, sans-serif;
  font-weight: bold;
  display: flex;
}

.page__container {
  max-width: 1194px;
}

.page__main {
  flex-direction: column;
  gap: 110px;
  margin-bottom: 20px;
  display: flex;
}

@media (width >= 640px) {
  .page__main {
    margin-top: 130px;
    margin-bottom: 30px;
  }
}

@media (width >= 1280px) {
  .page__main {
    margin-top: 90px;
  }
}

.page__title {
  color: #333;
  margin-bottom: 20px;
}

@media (width >= 640px) {
  .page__title {
    margin-bottom: 30px;
  }
}

.page__mainText {
  color: #333;
}

.page__secondaryText {
  color: #7c7c7c;
}

.header {
  z-index: 1000;
  background-color: #f7f7f7;
  justify-content: space-between;
  align-items: center;
  height: 76px;
  padding: 0 30px;
  display: flex;
  position: sticky;
  top: 0;
}

.header__logo {
  z-index: 100;
  height: 26px;
  position: relative;
}

.header__buttons {
  align-items: center;
  height: 100%;
  display: flex;
}

.header__specs {
  cursor: pointer;
  width: 72px;
  display: block;
}

.header__specs:hover {
  color: #0c797a;
}

.header__lang {
  display: none;
}

@media (width >= 640px) {
  .header__lang {
    display: flex;
  }
}

.header__langItem {
  cursor: pointer;
  color: #333;
}

.header__langItem:hover {
  color: #0c797a;
}

.header__langItem--unactive {
  color: #bdbdbd;
}

.header__buy {
  display: none;
}

@media (width >= 640px) {
  .header__buy {
    width: 102px;
    height: 48px;
    margin: 0 30px;
    display: block;
  }
}

@media (width >= 1280px) {
  .header__buy {
    margin-left: 47px;
    margin-right: 0;
  }
}

.scroll-padding {
  margin-top: -76px;
  padding-top: 76px;
}

.menu {
  z-index: 10;
  background-color: #f7f7f7;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 107px 30px 30px;
  display: flex;
  position: fixed;
  top: 0;
  right: -100%;
}

.menu__list {
  flex-direction: column;
  gap: 25px;
  list-style: none;
  display: flex;
}

.menu__item {
  color: #333;
  text-decoration: none;
}

.menu__item:hover {
  font-weight: 700;
}

.menu__specs {
  width: 72px;
  display: block;
}

.menu__lang {
  margin-top: 50px;
  display: flex;
}

@media (width >= 640px) {
  .menu__lang {
    display: none;
  }
}

.menu__langItem {
  color: #333;
}

.menu__langItem--unactive {
  color: #bdbdbd;
}

.menu__buy {
  width: 100%;
  height: 48px;
}

@media (width >= 640px) {
  .menu__buy {
    display: none;
  }
}

.menu--open {
  right: 0;
}

.icon {
  background-color: #0000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
  position: relative;
}

.icon--menu {
  z-index: 100;
  background-image: url("menu.78e8a919.svg");
}

@media (width >= 1280px) {
  .icon--menu {
    display: none;
  }
}

.icon--cross {
  z-index: 100;
  background-image: url("cross.4e246dfc.svg");
}

.button {
  text-align: center;
  color: #f7f7f7;
  background-color: #333;
}

.button:hover {
  background-color: #131313;
}

.button--disabled {
  background-color: #d0d0d0;
}

.button--green {
  background-color: #0db2b3;
}

.button--green:hover {
  background-color: #0c797a;
}

.button--green--disabled {
  background-color: #cfeff0;
}

.product__container {
  height: 100%;
}

@media (width >= 640px) {
  .product__container {
    position: relative;
  }
}

.product__imgContainer {
  width: calc(100% + 60px);
  margin-bottom: 40px;
  overflow: hidden;
  transform: translateX(-30px);
}

@media (width >= 640px) {
  .product__imgContainer {
    z-index: -1;
    width: 100%;
    height: 100%;
    min-height: 300px;
    margin: 0;
    position: absolute;
    transform: translateX(0);
  }
}

.product__img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.advantages__container {
  row-gap: 65px;
}

.advantages__item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.advantages__item--offMobile {
  display: none;
}

@media (width >= 640px) {
  .advantages__item--offMobile {
    display: flex;
  }
}

.advantages__icon {
  object-fit: contain;
  background-position: center;
  width: 50px;
  height: 40px;
  margin-bottom: 40px;
}

.advantages__icon--design {
  background-image: url("design.d0775aee.svg");
}

.advantages__icon--speaker {
  background-image: url("sound.03503820.svg");
}

.advantages__icon--multiroom {
  background-image: url("wifi.0700fe61.svg");
}

.advantages__icon--lighting {
  background-image: url("light.f8e3a337.svg");
}

.advantages__icon:hover {
  transform: scale(1.2);
}

.advantages__itemTitle {
  text-align: center;
}

@media (width >= 1280px) {
  .advantages__itemTitle {
    height: 56px;
  }
}

.advantages__text {
  text-align: center;
  margin-top: 20px;
}

.photoSection__img {
  object-fit: contain;
  width: 100%;
}

.about__container {
  grid-template-rows: auto;
  row-gap: 0;
}

@media (width >= 640px) {
  .about__container {
    grid-template-rows: 92px 1fr;
    row-gap: 30px;
  }

  .about__picture {
    grid-row: 1 / 3;
    width: calc(100% + 34px);
    transform: translateX(-34px);
  }

  .about__picture--revers {
    transform: translateX(0);
  }
}

@media (width >= 1280px) {
  .about__picture {
    width: 100%;
    transform: translateX(0);
  }
}

.about__img {
  object-fit: contain;
  width: 100%;
  margin: 30px 0 20px;
}

@media (width >= 640px) {
  .about__img {
    margin: 0;
  }
}

.about__button {
  color: #0db2b3;
  background-color: #0000;
  border: none;
  margin-top: 30px;
}

.about__button:after {
  content: "";
  background-color: #0db2b3;
  width: 54px;
  height: 1px;
  margin-top: 5px;
  transition: all .3s;
  display: block;
}

.about__button:hover:after {
  width: 100%;
}

.quote {
  padding-top: 50px;
  position: relative;
}

@media (width >= 640px) {
  .quote {
    padding-top: 42px;
  }
}

.quote__coma {
  z-index: -1;
  background-image: url("come.9a6f4cce.svg");
  width: 208px;
  height: 171px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media (width >= 640px) {
  .quote__coma {
    width: 233px;
    height: 189px;
  }
}

.quote__text {
  text-align: center;
}

.quote__autor {
  flex-direction: column;
  grid-column: 2 / 4;
  align-items: center;
  gap: 5px;
  margin-top: 30px;
  display: flex;
}

.quote__autorImg {
  object-fit: cover;
  border-radius: 50%;
  width: 62px;
  height: 62px;
  margin-bottom: 5px;
}

.slider__title {
  margin-left: 30px;
}

.slider__container {
  background-color: #d6ecec;
  margin-top: 30px;
  padding-top: 30px;
}

@media (width >= 640px) {
  .slider__container {
    padding-top: 60px;
  }
}

.slider__item {
  flex-direction: column;
  gap: 20px;
  min-height: 275px;
  display: flex;
}

.slider__item--disabled {
  display: none;
}

@media (width >= 1280px) {
  .slider__item--disabled {
    display: flex;
  }
}

.slider__itemTitle {
  color: #0c797a;
}

.slider__itemList {
  flex-direction: column;
  gap: 10px;
  list-style: none;
  display: flex;
}

.slider__li {
  color: #333;
  align-items: center;
  list-style-position: inside;
  display: flex;
}

.slider__li:before {
  content: "•";
  margin-right: 10px;
}

.slider__controller {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 100px;
  margin: 50px auto 0;
  display: flex;
}

@media (width >= 640px) {
  .slider__controller {
    margin: 50px 0 0;
  }
}

@media (width >= 1280px) {
  .slider__controller {
    display: none;
  }
}

.slider__button {
  background: url("sliderArrowNormal.b22d33d9.svg");
  width: 48px;
  height: 14px;
}

.slider__button--left {
  transform: rotate(180deg)translateX(-3px);
}

.slider__button--right {
  transform: translateX(-6px);
}

.slider__button--disabled {
  background-image: url("sliderArrow.634f0220.svg");
}

.slider__button:hover {
  background-image: url("sliderArrowHover.a3386d7a.svg");
}

.slider__selectedPage {
  color: #333;
}

.slider__page {
  color: #0c797a;
  font-size: 11px;
  font-weight: 400;
  line-height: 14px;
}

.slider__img {
  object-fit: contain;
  width: 100%;
  transform: translateY(5px);
}

@media (width >= 1280px) {
  .slider__photoContainer {
    grid-row: 2 / 3;
  }
}

.form__body {
  flex-direction: column;
  gap: 20px;
  margin-top: 27px;
  display: flex;
}

.form__field {
  box-sizing: border-box;
  color: #bdbdbd;
  background-color: #0000;
  border: 1px solid #828282;
  padding: 12px 0 12px 20px;
  font-family: Inter, sans-serif;
  display: block;
}

.form__field:hover {
  border: 1px solid #131313;
}

.form__field::placeholder {
  color: #bdbdbd;
  font-family: Inter, sans-serif;
}

.form__field:focus {
  color: #333;
  outline: none;
  font-family: Inter, sans-serif;
}

.form__field:-webkit-autofill {
  -webkit-text-fill-color: #bdbdbd;
  border: 1px solid #828282;
  font-family: Inter, sans-serif;
  box-shadow: inset 0 0 0 1000px #f7f7f7;
}

.form__field:-webkit-autofill:focus {
  -webkit-text-fill-color: #333;
  outline: none;
  font-family: Inter, sans-serif;
}

.form__field--area {
  resize: none;
  min-height: 128px;
}

@media (width >= 640px) {
  .form__field--area {
    min-height: 201px;
  }
}

@media (width >= 1280px) {
  .form__field--area {
    min-height: 128px;
  }
}

.form__button {
  width: 102px;
  height: 40px;
}

.footer {
  position: relative;
}

.footer__icons {
  gap: 40px;
  margin: 0 auto 30px;
  display: flex;
}

.footer__icon {
  object-fit: contain;
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}

